import { useEffect, useState } from 'react';

export function useDebounce(value: string, delay = 300): string {
  const [undo, setUndo] = useState(value);

  useEffect(() => {
    const handler = setTimeout(() => { setUndo(value); }, delay)
    return () => { clearTimeout(handler); }
  }, [value, delay])

  return undo;
};
